.list-main .table1{
  border-radius: 10px;
  background-color: white;
  min-width: 750px;
  overflow: hidden;
}

.list-main .table1 .top{
  display: flex;
  line-height: 35px;
  justify-content: space-between;
  white-space: nowrap;
  padding: 10px 20px 10px 10px;
}
.list-main .table1 .top .left .title{
  font-size: 18px;
  font-weight: 700;
}
.list-main .table1 .top .left .total{
  display: inline-block;
  width: 30px;
  height: 20px;
  line-height: 20px;
  border-radius: 8px;
  text-align: center;
  background-color: rgb(228, 228, 228);
  position: relative;
  left: 5px;
  bottom: 3px;
}
.list-main .table1 .top .right{
  display: flex;
}
.list-main .table1 .top .right input{
  height: 35px;
  border-radius: 5px 0 0 5px;
}
.ant-btn.ant-btn-primary.ant-input-search-button{
  height: 35px;
  border-radius: 0px 5px 5px 0px !important;
  line-height: 35px;
}
.list-main .table1 .content{
  padding-left: 15px;
}


.list-main .table1 .content table{
  width: 95%;
  white-space: nowrap;
}

.list-main .table1 .bottom{
  width: 100%;
  text-align: center;
  padding: 10px 0px;
}
#loadmore-btn{
  border: 1px solid rgb(64, 169, 255);
  border-radius: 5px;
}

#others-option-del{
  color: red;
}

/* -------------- Modal ---------------- */

.ant-row.ant-form-item{
  margin-bottom: 10px;
}
.uploadFile{
  position: relative;
  width: 150px;
  height: 32px;
  bottom: 27px;
  right: 16px;
  opacity: 0;
}